
<script type="text/javascript">
import Vue from 'vue'

Vue.component('item-tree', {
  template: `
    <div>
    <li class="item-tree" v-if="'' != model.DirName">
      <div>
        <nobr>
        <span class="item" @click="toggle" :class="{'active': isFolder}">
        <!--<span v-if="isFolder">
          <i v-if="open" class="fa fa-minus-square" aria-hidden="true"></i>
          <i v-if="!open" class="fa fa-plus-square" aria-hidden="true"></i>
        </span>-->
        <i class="fa" :class="{'fa-folder-open': open, 'fa-folder': !open}" aria-hidden="true"></i>
        {{model.DirName}}
        </span>
        </nobr>
        <div class="btn-tree-opt-group">
          <span @click.stop class="btn-tree-opt"><a @click="addChild"><i class="fa fa-plus-square" aria-hidden="true"></i></a></span>
          <span @click.stop class="btn-tree-opt"><a @click="removeSelf"><i class="fa fa-minus-square" aria-hidden="true"></i></a></span>
          <span @click.stop class="btn-tree-opt"><a @click="editSelf"><i class="fa fa-pencil-square" aria-hidden="true"></i></a></span>
        </div>
      </div>
      <ul v-show="open" v-if="isFolder">
        <item-tree
          v-for = "(model, index) in model.SubDirs"
          :key = "index"
          :model = "model" >
        </item-tree>
      </ul>
    </li>
    <b-modal centered :id="'modalPrevent' + this.model.ID"
             :ref="'modal' + this.model.ID"
             :title="$t('请输入目录名称')"
             @ok="handleOk">
      <form @submit.stop.prevent="handleSubmit">
        <b-form-input type="text"
                      :placeholder="$t('请输入目录名称')"
                      v-model="model.DirName"></b-form-input>
      </form>
    </b-modal>
    </div>
  `,
  props: {
    model: Object
  },
  data: function () {
    return {
      open: false,
      dirName: '',
      isEdit: false
    }
  },
  computed: {
    isFolder: function () {
      return this.model.SubDirs && this.model.SubDirs.length
    }
  },
  methods: {
    toggle: function () {
      if (this.isFolder) {
        this.open = !this.open
      }
    },
    addChild: function () {
      if (!this.model.SubDirs || !(this.model.SubDirs instanceof Array)) {
        Vue.set(this.model, 'SubDirs', [])
      }
      this.open = true

      this.isEdit = false
      this.dirName = this.model.DirName
      console.log(this.dirName)
      this.$refs['modal' + this.model.ID].show()
    },
    removeChild: function (ID) {
      var subDirs = this.model.SubDirs
      for (var i = 0; i < subDirs.length; ++i) {
        if (ID === subDirs[i].ID) {
          subDirs.splice(i, 1)
          this.model.SubDirs = subDirs
          console.log(this.model.SubDirs)
          return
        }
      }
    },
    removeSelf: function () {
      if (this.$parent.removeChild) {
        this.$parent.removeChild(this.model.ID)
      }
    },
    editSelf: function () {
      this.isEdit = true
      this.dirName = this.model.DirName
      this.$refs['modal' + this.model.ID].show()
    },
    handleOk (evt) {
      // Prevent modal from closing
      evt.preventDefault()
      if (!this.model.DirName) {
        alert('Please enter your name')
      } else {
        this.handleSubmit()
      }
    },
    handleSubmit () {
      if (!this.isEdit) {
        this.model.SubDirs.push({
          ID: '' + new Date().getTime(),
          DirName: this.model.DirName
        })
      }
      this.$refs['modal' + this.model.ID].hide()
    }
  }
})
</script>



