<template>
  <div class="main-container">
    <div class="left-container">
      <ul class="dir-tree">
        <item-tree
          class="item"
          :model="allDirs">

        </item-tree>
      </ul>
    </div>
    <div class="right-container">
      <div class="right-innter-container">
        <img src="../assets/images/logo.png">
        <div>
          Hello {{$route.params.id}} ...
        </div>
      </div>
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: 'Dir',
  computed: {
    allDirs () {
      return this.$Util.getDir(this, this.$route.params.id)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



